html {
  scroll-behavior: smooth; /* Enable smooth scrolling */
}

h1, h2, h3 {
  margin: 20px 0; /* Added margin for better separation */
  font-family: 'Playfair Display', serif;
  font-size: xx-large;
}

p, span {
  font-family: 'Inter', sans-serif;
}

/* Add your global styles here */

@media (max-width: 768px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
.bar-component {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.square {
  margin: 10px;
  text-align: center;
}

.square .icon {
  font-size: 24px; /* Medium size for icons */
}

.square p {
  font-weight: bold; /* Bold text */
}

.square:hover {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); /* Shadow effect on hover */
}

.vision {
  text-align: center;
  margin: 20px 0;
}

.vision-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.vision-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
  width: 200px; /* Updated width */
  text-align: center;
  transition: box-shadow 0.3s; /* Smooth transition for shadow */
}

.vision-card:hover {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2); /* Shadow effect on hover */
}

.hero {
  height: 400px; /* Adjust height as needed */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
}

.hero-button {
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.8);
  color: black;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.hero-button:hover {
  background-color: rgba(255, 255, 255, 1);
}

.about-us {
  display: flex;
  justify-content: center; /* Center the content */
  margin: 20px 0;
  flex-direction: column; /* Stack the heading and content vertically */
  align-items: center; /* Center items horizontally */
}

.about-us h2 {
  text-align: center; /* Center the heading text */
}

.about-us-content {
  display: flex;
  width: 100%;
}

.description {
  flex: 1;
  padding: 20px;
}

.video {
  flex: 1;
  padding: 20px;
}

.video iframe {
  width: 100%;
  height: 100%;
}

.projects {
  text-align: center;
  margin: 20px 0;
}

.project-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.project-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
  width: 200px; /* Adjust width as needed */
  text-align: center;
  transition: transform 0.3s; /* Smooth transition for scale */
}

.project-card:hover {
  transform: scale(1.1); /* Scale up the hovered card */
}

.news {
  text-align: center;
  margin: 20px 0;
}

.news-items {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.news-item {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
  width: 400px; /* Adjust width as needed */
  margin-bottom: 20px;
  text-align: center;
}

.news-item h3 {
  font-size: large;
}

/* New section border class */
.section-border {
    border: 1px solid lightgrey; /* Light grey border */
    padding: 10px; /* Optional padding */
    margin: 10px 0; /* Optional margin */
    background-color: white; /* White background */
}
